<template>
  <div class="project-all-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="list"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <div class="project-all-list-title">全部融资项目</div>
           <pem-project-list :data="list" @alert="alert"/>
          <div class="scroll-view-pullup">{{isMore ? '加载中...' : '暂时就这么多了...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" bottom="344"/>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemDownloadBar from 'components/download-bar/download-bar'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { PROJECT_ALL_LIST_CLEAR, PROJECT_ALL_LIST_UPDATE } from 'store/minisite/project-all-list'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemProjectList,
    PemDownloadBar,
    PemScrollView,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      list: [],
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    projectAllList: state => state.projectAllList
  }),
  created () {
    this._initWXShare()
    this._setTitle('项目工场-项目')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  beforeDestroy () {
    let _projectAllList = this.projectAllList
    _projectAllList.y = this.$refs.scroll.scroll.y
    this.PROJECT_ALL_LIST_UPDATE(_projectAllList)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getProjectList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getProjectList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getProjectList('init')
      }
    },
    getProjectList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.projectAllList &&
        this.projectAllList.list &&
        this.projectAllList.list.length > 0 &&
        this.currentPage <= this.projectAllList.currentPage) {
        this.isLoading = false
        this.currentPage = this.projectAllList.currentPage + 1
        if (+this.projectAllList.totalPage <= +this.projectAllList.currentPage) {
          this.isMore = false
        }
        this.list = this.projectAllList.list || []
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.projectAllList.y)
        }, 21)
        return false
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize,
        sorttype: '300'
      }
      this._getProjectList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          _list = _list.concat(data.resultList || []).map(item => {
            item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
            if (item.iconList) {
              item.iconList = item.iconList.map(icon => {
                let _imgurl = icon.imgurl
                icon.imgurl = _imgurl ? (ImgPath + _imgurl.replace(ImgPath, '')) : null
                return icon
              })
            }
            return item
          })
          this.PROJECT_ALL_LIST_CLEAR()
          this.PROJECT_ALL_LIST_UPDATE({
            list: _list,
            currentPage: +data.currentPage || 1,
            totalPage: +data.totalPage || 1,
            y: 0
          })
          this.list = _list
        }
      })
    },
    _getProjectList (options, callback) {
      MiniSiteApi.projectList(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      PROJECT_ALL_LIST_UPDATE,
      PROJECT_ALL_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
  .project-all-list-title
    padding: 0.4rem 0.3rem 0
    font-size: 0.36rem
    font-weight: bold
    line-height: 0.5rem
    color: $color-text
</style>


